/* This is the style for the carousel container */
.carousel-container {
    position: relative;
    width: 80%; /* You can adjust the width as needed */
    margin: auto; /* This centers the carousel horizontally */
    max-width: 600px; /* Maximum width, feel free to adjust */
  }
  
  /* Style for navigation arrows */
  .left-arrow, .right-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    background-color: #fff;
    border: none;
    padding: 10px;
    z-index: 2;
  }
  
  .left-arrow {
    left: 10px;
  }
  
  .right-arrow {
    right: 10px;
  }
  
  /* Style for the main image display */
  .carousel-main-image {
    position: relative;
    overflow: hidden; /* Ensures no overflow of the content */
    margin-bottom: 20px; /* Space for thumbnails */
  }
  
  .carousel-main-image img {
    width: 100%; /* Makes image responsive */
    display: block; /* Removes extra space below the image */
    margin: auto; /* Centers image in the div */
  }
  
  /* Style for the thumbnail navigation */
  .carousel-thumbnails {
    display: flex;
    justify-content: center;
    gap: 5px; /* Spacing between thumbnails */
  }
  
  .thumbnail {
    width: 60px; /* Smaller size for thumbnails */
    cursor: pointer;
    opacity: 0.6;
    transition: opacity 0.3s ease;
  }
  
  .thumbnail.active {
    opacity: 1; /* Active thumbnail has full opacity */
  }
  
  /* Additional styles to center the carousel vertically */
  body, html {
    height: 100%;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  /* Ensures the content is wrapped correctly */
  #root {
    width: 100%;
  }
  