.Sidebar {
    position: fixed; /* Fix the sidebar to the viewport */
    top: 50; /* Align the top of the sidebar with the top of the viewport */
    right: 0; /* Align the sidebar to the right of the viewport */
    width: 250px; /* Width of the sidebar */
    background-color: #ffffff; /* Background color of the sidebar */
    padding: 10px;
    
    margin-top: 10em;
    margin-right: 2em;
    margin-bottom: 1em;
    box-shadow: -2px 0 5px rgba(0, 0, 0, 0.2); /* Optional: adds shadow to the left of the sidebar */
    z-index: 10; /* Ensure the sidebar is above other elements */
    height: auto; /* Optional: if you want the sidebar to span the entire height */
  }

  .Sidebar h2 {
    margin-bottom: 10px;
    color: #0047AB; /* Adjust the color to match your design */
  }
  
  .event {
    
    align-items: center;
    background-color: #0047AB; 
    color: white;
    margin-bottom: 10px;
    padding: 10px;
  }

  .event-detail {
    display: flex;
    flex-direction: column;
  }
  
  .event-date {
    
    
    font-weight: bold;
    margin-right: 10px; 
  }
  
  .event-description {
    
    
    color: white; 
    text-decoration: none; 
  }

  .event-description:hover {
    text-decoration: underline; 
    
  }

  @media (max-width: 780px) {
    .Sidebar {
      display: none; /* This will hide the Sidebar */
    }
  }
