.Navbar {
  background-color: #0047AB; /* Adjust the color to match your design */
  padding: 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed; /* This will make the navbar fixed at the top */
  top: 0; /* Align the navbar to the top */
  left: 0; /* Align the navbar to the left */
  right: 0; /* Ensure the navbar extends to the right */
  z-index: 1000; /* Ensure the navbar is above other content */
}

.Navbar-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Navbar-logo {
  height: 50px; /* Adjust size as necessary */
  margin-left: 20px; /* Space from the left edge */
}

.Navbar-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
}

.Navbar-item {
  margin: 0 20px; /* Adjust spacing to match your design */
}

.Navbar-item a {
  text-decoration: none;
  color: white;
  font-size: 18px;
  transition: color 0.3s ease;
}

.Navbar-item a:hover {
  color: #aaa; /* Hover effect color */
}